<template>
  <Modal
    :active="showModal"
    :event-name="'product-quotation-select-modal'"
    :custom-width="'w-full ipad:w-[768px]'"
    :full-screen="'nexus'"
    :custom-padding="'p-spacing-0'"
    :prevent-close="true"
  >
    <ProductQuotationSelectForm
      v-if="products"
      :products="products"
      :quotation-files="quotationFiles"
    />
  </Modal>
</template>

<script>
import Modal from "@/components/General/Modal.vue";
export default {
  components: {
    Modal,
    ProductQuotationSelectForm: () =>
      import("@/components/MarketResearch/ProductQuotationSelectForm.vue"),
  },
  data() {
    return {
      showModal: false,
      products: null,
      quotationFiles: [],
    };
  },
  mounted() {
    this.$bus.$on("product-quotation-select-modal", (data) => {
      if (data?.showModal) {
        this.showModal = data?.showModal;
        this.products = data?.products;
        this.quotationFiles = data?.quotationFiles;
      } else {
        this.showModal = false;
        this.products = null;
      }
    });
  },
  beforeDestroy() {
    this.$bus.$off("product-quotation-select-modal");
  },
};
</script>
